.block {
  display: block;
}

.font-normal {
  font-weight: normal;
}

.background-info {
  background: #9b59b6;
  color: #ffffff;
}

.background-success {
  background: #2ecc71;
  color: #ffffff;
}

.background-warning {
  background: #f39c12;
  color: #ffffff;
}

.background-danger {
  background: #e74c3c;
  color: #ffffff;
}

.border-round {
  border-radius: 30px;
}

.border-flat {
  border-radius: 0;
}

.border-circle {
  border-radius: 50%;
}

.text-ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  width: 150px;
}

.text-primary {
  color: #2ecc71;
}

.text-warning {
  color: #f39c12;
}

.text-secondary {
  color: #5867dd;
}

.text-center {
  text-align: center;
}

.po-absolute {
  position: absolute;
}

.po-relative {
  position: relative;
}

.w-100 {
  width: 100%;
}

svg:not(:root) {
  overflow: hidden;
}

@media only screen and (min-width: 768px) {
  .text-right-md {
    text-align: right;
  }
}

.fill-dark {
  fill: #1e2022;
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.text-small {
  font-size: 12px;
}

.text-white {
  color: #fff;
}

.text-white h3, .text-white p {
  color: #fff;
}

.text-white .icon {
  color: #fff;
}

.text-white--light {
  color: rgba(255, 255, 255, 0.7);
}

.text-white .list__link {
  color: rgba(255, 255, 255, 0.7);
}

.text-white .list__link:hover, .text-white .list__link:focus {
  color: #fff;
}

.text-black {
  color: #1e2022;
}

.text-black h3, .text-black p {
  color: #1e2022;
}

.text-black .icon {
  color: #1e2022;
}

.text-black--light {
  color: rgba(30, 32, 34, 0.7);
}

.text-black .list__link {
  color: rgba(30, 32, 34, 0.7);
}

.text-black .list__link:hover, .text-black .list__link:focus {
  color: #1e2022;
}
